/**
* axios二次封装 防抖与异常处理
* author: harran
* time: 2023/6/29
* @params: {
 *     url: '', 请求url
 *     method: '', 请求方式
 *     data: {}, 请求数据- 不区分post与get
 *     success: function, 请求成功回调
 *     error: function, 请求失败回调
 *     always: function 总会执行的回调
 * }
* @return: Promise
*/
import axios from 'axios';

const DEFAULTCONFIG = {
    proxy: process.env.NODE_ENV === 'development',
    baseURL: process.env.NODE_ENV === 'development' ? '/api' : '',
    timeout: 2000
}

const DEFAULT = {
    method: 'post', //请求method
    url: '', //请求url
    data: {}, //参数
    query: {},
    success: function() {}, // 成功的回调函数
    error: function() {}, // 错误的回调函数
    always: function() {} //完成的回调函数
};

class Request {
    configSource = {}; //存储起来的传入的请求配置
    config = {}; //请求参数
    loading = false; //请求状态标识
    cancel = null; //请求取消标识
    curAxios = null; //当前请求的axios对象
    timer = null; //防抖计时器
    debounceTimerLength = 500; //请求防抖时间时长

    successCallback = function() {};
    errorCallback = function() {};
    alwaysCallback = function() {};

    constructor() {
        let that = this;
        //创建当前请求所用的axios对象
        that.curAxios = axios.create(DEFAULTCONFIG);

        // 添加请求拦截器
        this.curAxios.interceptors.request.use(
            function(config) {
                // 在发送请求之前做些什么
                return config;
            },
            function(error) {
                that.loading = false;
                // 对请求错误做些什么
                return Promise.reject(error);
            }
        );

        // 添加响应拦截器
        this.curAxios.interceptors.response.use(
            function(response) {
                that.loading = false;
                // 对响应数据做点什么
                return response;
            },
            function(error) {
                that.loading = false;
                // 对响应错误做点什么
                return Promise.reject(error);
            }
        );
    }

    updateConfig(cfg) {
        let that = this;
        this.configSource = Object.assign({}, cfg);
        let tempConfig = Object.assign({}, DEFAULT, this.configSource);
        this.config = Object.assign({}, tempConfig);
        if (this.config.method === 'post') {
            this.config.params = {}
            this.config.data = this.config.data || {}
        } else {
            this.config.params = this.config.data || {}
            this.config.data = {}
        }
        //重置请求防抖间隔时长
        if (tempConfig.debounceTimerLength !== undefined) {
            this.debounceTimerLength = tempConfig.debounceTimerLength;
        }
        //初始化请求成功回调函数
        if (tempConfig.success) {
            this.successCallback = tempConfig.success;
        }
        //初始化请求失败回调函数
        if (tempConfig.error) {
            this.errorCallback = tempConfig.error;
        }
        //初始化请求不管成功与否一定会执行的函数
        if (tempConfig.always) {
            this.alwaysCallback = tempConfig.always;
        }
        //添加时间戳
        this.config.data['timestamp'] = new Date().getTime();

        // 添加取消请求标识
        let CancelToken = axios.CancelToken;
        that.config.cancelToken = new CancelToken(function executor(c) {
            // executor 函数接收一个 cancel 函数作为参数
            that.cancel = c;
        });
        return this.config
    }

    /**
     * 直接发起请求
     */
    request(config) {
        let that = this;
        //更新请求状态标识
        that.loading = true;
        //初始化可取消请求控制器
        that.cancel = null;
        //保存当前请求
        that.curAxios
            .request(that.updateConfig(config))
            .then(function(data) {
                that.successCallback(data);
            })
            .catch(function() {
                that.errorCallback(arguments);
            })
            .finally(function() {
                that.alwaysCallback(arguments);
            });
    }

    /**
     * 发起防抖请求
     */
    debounceRequest(config) {
        let that = this;
        if (that.timer !== null) {
            window.clearTimeout(that.timer);
            that.timer = null;
        }
        that.timer = window.setTimeout(function() {
            //更新请求状态标识
            that.loading = true;
            //初始化可取消请求控制器
            that.cancel = null;
            //开始请求
            that.curAxios
                .request(that.updateConfig(config))
                .then(function(data) {
                    that.successCallback(data);
                })
                .catch(function() {
                    that.errorCallback(arguments);
                })
                .finally(function() {
                    that.alwaysCallback(arguments);
                });
        }, that.debounceTimerLength);
    }

    /**
     * 取消当前请求
     */
    cancelRequest() {
        if (this.timer) {
            window.clearTimeout(this.timer);
        }
        if (this.cancel) {
            this.cancel();
        }
    }
}

export default Request;